.listItem {
  width: 225px;
  height: 120px;
  background-color: var(--main-color);
  margin-right: 5px;

  color: white;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  video {
    width: 100%;
    height: 140px;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
  }
  &:hover {
    width: 325px;
    height: 300px;
    position: absolute;
    top: -150px;
    box-shadow: 0px 0px 15px 0px rgba(255, 255, 255, 0.07);
    border-radius: 5px;
    img {
      height: 140px;
      border-radius: 5px 5px 0 0;
    }
    .info {
      display: flex;
      flex-direction: column;
      padding: 5px;
      .icons {
        display: flex;
        margin-bottom: 10px;
        .icon {
          border: 2px solid white;
          padding: 5px;
          border-radius: 50%;
          margin-right: 10px;
          font-size: 16px;
        }
      }
      &__top {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        font-family: 600;
        font-size: 14px;
        color: gray;
        .limit {
          border: 1px solid gray;
          padding: 1px 3px;
          margin: 0 10px;
        }
      }
      .desc {
        font-size: 13px;
        margin-bottom: 10px;
      }
      .genre {
        color: lightgray;
        font-size: 15px;
      }
    }
  }
}
